* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  margin: 0;
}

ul {
  list-style: none;
}

.header {
  height: 80px;
  padding-top: 25px;
  background: rgb(23, 69, 218);
}

/* * Form selectors */

.search-form {
  display: flex;
  align-items: center;
  justify-content: center;
}

.form__input {
  padding: 8px;
  width: 250px;
  border-right: 0;
  border-radius: 5px 0 0 5px;
}

.form__button {
  padding: 8px;
  border-left: 0;
  border-radius: 0 5px 5px 0;

  cursor: pointer;
}

/* todo Gallery Cards */

.gallery {
  display: flex;
  flex-wrap: wrap;
}

.photo-card {
  margin-right: 20px;
  margin-bottom: 20px;
  width: calc((100% - 80px) / 4);
}

